<template>
  <div id="translator-area">
    <div class="card">
      <div class="card-header text-left">Bản dịch</div>
      <div class="card-body">
        <template v-if="novel_content.length>0">
          <div
          class="row border p-1"
          v-for="(paragraph, index) in novel_content"
          :key="'paragraph-'+index"
          :id="'paragraph-'+index"
        >
          <div class="col-5">
            <AutosizeTextarea class="form-control" :id="'p-source-'+index" v-model="paragraph.source"/>
          </div>
          <div class="col-1">
            <button class="btn btn-info">
              <font-awesome-icon icon="copy"/>
            </button>
            <button class="btn btn-info" @click="autoTranslate(index)">
              <font-awesome-icon icon="magic" flip="horizontal"/>
            </button>
          </div>
          <div class="col-5">
            <AutosizeTextarea class="form-control" :id="'p-destination-'+index"  v-model="paragraph.translator"/>
          </div>
          <div class="col-1">
            <button class="btn btn-primary">
              <font-awesome-icon icon="plus"/>
            </button>
            <button class="btn btn-danger">
              <font-awesome-icon icon="minus"/>
            </button>
          </div>
        </div>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
import AutosizeTextarea from "./AutosizeTextarea.vue";
var translate = require("yandex-translate")(
  "trnsl.1.1.20190216T021308Z.bf3d5c5f3c4af641.8d4cd659c5e3cc174a8014355ef031b23c66cf16"
);
export default {
  data() {
    return {
      novel_name: "",
      novel_volume: "",
      novel_chapter: "",
      novel_content: [{source:"", translator:""}]
    };
  },
  methods: {
    autoTranslate: function(id) {
      translate.translate(this.novel_content[id].source, { from: "en", to: "vi" }, function(
        err,
        res
      ) {
        this.novel_content[id].translator=res.text[0]
      }.bind(this));
    }
  },
  components: {
    AutosizeTextarea
  }
};
</script>


