<template>
  <textarea @input="updateContent" :value="value" :name="name"/>
</template>
<script>
import autosize from 'autosize';
export default {
  props: {
    name: {
      default: ""
    },
    value: {
      default: ""
    },
    rows: {
      default: 1
    },
    placeholder: {
      default: ""
    }
  },
  mounted() {
    autosize(this.$el);
  },
  watch:{
    value: function() {
      autosize.update(this.$el);
    }
  },
  methods: {
    updateContent() {
      this.$emit("input", this.$el.value);
    }
  }
};
</script>